import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ExtAuthService } from '../../services/ext-auth.service';
import { IExtUser } from '@shared/interfaces';
import { IUserBalance } from '@ngx-common/models/user-balance.models';
import { ExtPaymentService } from '../../services/ext-payment.service';
import { EPaymentPlanType } from '@ngx-common/enums/payment-plan-type.enum';

@Component({
	selector: 'app-error-page',
	templateUrl: './error-page.component.html',
	styleUrls: ['./error-page.component.scss'],
})
export class ErrorPageComponent implements OnInit {
	user: IExtUser;
	userBalance: IUserBalance;
	isOrganizationMember: boolean;
	errMsg: string;
	hideBottomMessage: boolean = false;

	constructor(
		private _authService: ExtAuthService,
		private _paymentSvc: ExtPaymentService,
		private _activateRoute: ActivatedRoute
	) {}

	async ngOnInit() {
		this.userBalance = await this._paymentSvc.getExtensionUserBalanceAsync();
		this.isOrganizationMember = this.userBalance.accountType == EPaymentPlanType.SharedAccount;
		this.hideBottomMessage = this._activateRoute.snapshot?.queryParams?.hideBottomMessage;

		this.user = this._authService.user;
		this.errMsg =
			this._activateRoute.snapshot?.queryParams['errormsg'] ?? 'something went wrong, please try again later';
	}

	goToLink() {
		let tabURl = 'https://copyleaks.com';
		chrome.tabs.create({ url: tabURl });
	}
}
